import Upload from '../Upload'
import ChatFace from '../ChatFace'
import { addNormalMsg } from '@/api/'
export default {
  data () {
    return {
      textImgVisible: false,
      base64: '',
      showFace: false,
      msg: ''
    }
  },
  methods: {
    sendMsg () {
      if (!this.msg && !this.base64) {
        this.$vux.toast.show({
          text: '',
          width: '12em'
        })
        return false
      }
      addNormalMsg({ht_id: this.$route.params.id, content: this.msg, img: this.base64}).then((res) => {
        this.$vux.toast.show({
          text: ''
        })
        this.deleteMsgImg()
        this.msg = ''
        this.showFace = false
      })
    },
    msgImgUploadPreview (base64) {
      this.textImgVisible = true
      this.base64 = base64
    },
    deleteMsgImg () {
      this.textImgVisible = false
      this.base64 = ''
    },
    handleFaceInput (value) {
      this.msg = this.msg + value
      this.moveEnd(this.$refs.msg)
    },
    moveEnd (obj) {
      // obj.focus()
      var len = obj.value.length
      if (document.selection) {
        var sel = obj.createTextRange()
        sel.moveStart('character', len)
        sel.collapse()
        sel.select()
      } else if (typeof obj.selectionStart === 'number' && typeof obj.selectionEnd === 'number') {
        obj.selectionStart = obj.selectionEnd = len
      }
    }
  },
  components: {
    Upload,
    ChatFace
  }
}



